const VolumeBar = (props) => {
  const { payload, x, y, width, height } = props;
  
  if (!payload) return null;
  
  const isRising = payload.close >= payload.open;
  const color = isRising ? '#e74c3c' : '#2980b9';
  
  return (
    <rect
      x={x}
      y={y}
      width={width}
      height={height}
      fill={color}
      opacity={0.7}
    />
  );
};

const StockChart = ({ symbol, stockInfo }) => {
  const [chartData, setChartData] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [selectedPeriod, setSelectedPeriod] = useState(30);
  const [chartType, setChartType] = useState('line'); // 'line' or 'candle'
  const [interval, setInterval] = useState('daily'); // 'daily', 'weekly', 'monthly'
  const [chartInfo, setChartInfo] = useState(null);
  const [showMovingAverages, setShowMovingAverages] = useState({
    ma5: true,
    ma20: true,
    ma60: false,
    ma120: false
  });

  const periods = [
    { label: '1', value: 1 },
    { label: '1', value: 7 },
    { label: '1', value: 30 },
    { label: '3', value: 90 },
    { label: '6', value: 180 },
    { label: '1', value: 365 }
  ];

  const intervals = [
    { label: '', value: 'daily' },
    { label: '', value: 'weekly' },
    { label: '', value: 'monthly' }
  ];

  useEffect(() => {
    loadChartData();
  }, [symbol, selectedPeriod, interval]);
  
  //     
  useEffect(() => {
    //      
    console.log('  :', showMovingAverages);
  }, [showMovingAverages]);

  const loadChartData = async () => {
    try {
      setLoading(true);
      setError('');
      
      const response = await stockService.getStockHistory(symbol, selectedPeriod, interval);
      
      console.log('  :', response.data.slice(0, 3));
      
      //   
      const today = new Date();
      today.setHours(23, 59, 59, 999); //    
      
      //    ,   ,   
      let formattedData = response.data
        .filter(item => {
          const itemDate = new Date(item.date);
          //    
          return itemDate <= today && 
                 typeof item.open === 'number' && item.open > 0 &&
                 typeof item.close === 'number' && item.close > 0 &&
                 typeof item.high === 'number' && item.high > 0 &&
                 typeof item.low === 'number' && item.low > 0 &&
                 item.high >= item.low; //  >= 
        })
        .map(item => ({
          ...item,
          symbol: symbol, //    
          displayDate: new Date(item.date).toLocaleDateString('ko-KR', {
            month: 'short',
            day: 'numeric',
            ...(selectedPeriod >= 90 && { year: '2-digit' })
          })
        }));
      
      console.log('  :', formattedData.slice(0, 3));
      
      //   
      formattedData = calculateMovingAverages(formattedData, [5, 20, 60, 120]);
      
      console.log(`  :  ${response.data.length} ->   ${formattedData.length}`);
      
      setChartData(formattedData);
      
      //    (   chart_info )
      if (formattedData.length > 0 && formattedData[0].chart_info) {
        setChartInfo(formattedData[0].chart_info);
      }
      
    } catch (error) {
      console.error('Chart data loading error:', error);
      setError(formatErrorMessage(error));
    } finally {
      setLoading(false);
    }
  };

  const market = getMarketFromSymbol(symbol);
  const isKorean = market === 'KRW';
  const isOneDayChart = selectedPeriod === 1;
  
  //    ( )
  const movingAverageColors = {
    ma5: '#FFA500',   // 
    ma20: '#FF1493',  //  
    ma60: '#32CD32',  //  
    ma120: '#9932CC'  // 
  };
  
  //   
  const toggleMovingAverage = (ma) => {
    setShowMovingAverages(prev => ({
      ...prev,
      [ma]: !prev[ma]
    }));
  };
  
  // 1    ( )
  const availableChartTypes = isOneDayChart && !isKorean 
    ? [{ label: '', value: 'line' }] 
    : [
        { label: '', value: 'line' },
        { label: '', value: 'candle' }
      ];

  // Y   (    )
  const getYAxisDomain = () => {
    if (!chartData.length) return ['auto', 'auto'];
    
    //    
    const prices = [];
    
    chartData.forEach(item => {
      // OHLC  
      if (typeof item.high === 'number' && item.high > 0) prices.push(item.high);
      if (typeof item.low === 'number' && item.low > 0) prices.push(item.low);
      if (typeof item.open === 'number' && item.open > 0) prices.push(item.open);
      if (typeof item.close === 'number' && item.close > 0) prices.push(item.close);
      
      //    ( )
      Object.entries(showMovingAverages).forEach(([ma, isShown]) => {
        if (isShown && typeof item[ma] === 'number' && item[ma] > 0) {
          prices.push(item[ma]);
        }
      });
    });
    
    if (prices.length === 0) return ['auto', 'auto'];
    
    const actualMin = Math.min(...prices);
    const actualMax = Math.max(...prices);
    const priceRange = actualMax - actualMin;
    
    //   :         
    const paddingPercent = chartType === 'candle' ? 0.15 : 0.08; // : 15%, : 8%
    const padding = priceRange * paddingPercent;
    
    const minDomain = Math.max(0, actualMin - padding);
    const maxDomain = actualMax + padding;
    
    return [minDomain, maxDomain];
  };

  //  Y  
  const getVolumeDomain = () => {
    if (!chartData.length) return [0, 'auto'];
    
    const volumes = chartData.map(d => d.volume || 0).filter(v => v > 0);
    if (volumes.length === 0) return [0, 'auto'];
    
    const maxVolume = Math.max(...volumes);
    //    20%  
    return [0, maxVolume * 1.2];
  };

  //   
  const getChartStats = () => {
    if (!chartData.length) return null;
    
    const firstPrice = chartData[0].close;
    const lastPrice = chartData[chartData.length - 1].close;
    const change = lastPrice - firstPrice;
    const changePercent = (change / firstPrice) * 100;
    
    return {
      firstPrice,
      lastPrice,
      change,
      changePercent,
      highest: chartInfo?.max_price,
      lowest: chartInfo?.min_price
    };
  };

  const stats = getChartStats();
  
  const formatPrice = (price) => {
    return isKorean 
      ? `${Math.round(price).toLocaleString()}` 
      : `$${price.toFixed(2)}`;
  };

  return (
    <ChartContainer>
      <ChartHeader>
        <StockInfo>
          <div className="symbol">{symbol}</div>
          {stockInfo && <div className="name">{stockInfo.name}</div>}
        </StockInfo>
        
        <ChartControls>
          <ControlGroup>
            <ControlLabel></ControlLabel>
            <PeriodButtons>
              {periods.map((period) => (
                <PeriodButton
                  key={period.value}
                  active={selectedPeriod === period.value}
                  onClick={() => setSelectedPeriod(period.value)}
                >
                  {period.label}
                </PeriodButton>
              ))}
            </PeriodButtons>
          </ControlGroup>
          
          <ControlGroup>
            <ControlLabel></ControlLabel>
            <ChartTypeButtons>
              {availableChartTypes.map((type) => (
                <ChartTypeButton
                  key={type.value}
                  active={chartType === type.value}
                  onClick={() => setChartType(type.value)}
                >
                  {type.label}
                </ChartTypeButton>
              ))}
            </ChartTypeButtons>
          </ControlGroup>
          
          {chartType === 'candle' && (
            <ControlGroup>
              <ControlLabel></ControlLabel>
              <IntervalButtons>
                {intervals.map((int) => (
                  <IntervalButton
                    key={int.value}
                    active={interval === int.value}
                    onClick={() => setInterval(int.value)}
                    disabled={selectedPeriod === 1} // 1    
                  >
                    {int.label}
                  </IntervalButton>
                ))}
              </IntervalButtons>
            </ControlGroup>
          )}
          
          {/*   */}
          <ControlGroup>
            <ControlLabel>MA</ControlLabel>
            <MovingAverageButtons>
              {Object.entries(movingAverageColors).map(([ma, color]) => (
                <MAButton
                  key={ma}
                  active={showMovingAverages[ma]}
                  color={color}
                  onClick={() => toggleMovingAverage(ma)}
                >
                  {ma.replace('ma', '')}
                </MAButton>
              ))}
            </MovingAverageButtons>
          </ControlGroup>
        </ChartControls>
      </ChartHeader>

      {error && <ErrorMessage>{error}</ErrorMessage>}

      <ChartContent>
        {stats && (
          <ChartStats>
            <StatItem>
              <div className="label"></div>
              <div className="value">{formatPrice(stats.firstPrice)}</div>
            </StatItem>
            <StatItem>
              <div className="label"></div>
              <div className="value">{formatPrice(stats.lastPrice)}</div>
            </StatItem>
            <StatItem color={stats.change >= 0 ? '#e74c3c' : '#2980b9'}>
              <div className="label"></div>
              <div className="value">
                {stats.change >= 0 ? '+' : ''}{formatPrice(Math.abs(stats.change))}
                ({stats.changePercent >= 0 ? '+' : ''}{stats.changePercent.toFixed(2)}%)
              </div>
            </StatItem>
            {stats.highest && (
              <StatItem color="#e74c3c">
                <div className="label"></div>
                <div className="value">{formatPrice(stats.highest)}</div>
              </StatItem>
            )}
            {stats.lowest && (
              <StatItem color="#3498db">
                <div className="label"></div>
                <div className="value">{formatPrice(stats.lowest)}</div>
              </StatItem>
            )}
          </ChartStats>
        )}
        
        {loading ? (
          <LoadingState>
            <div className="spinner"></div>
          </LoadingState>
        ) : chartData.length > 0 ? (
          <>
            <div style={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
            {/*   () */}
            <div style={{ height: '70%', marginBottom: '10px' }}>
              {chartType === 'candle' ? (
                <ResponsiveContainer width="100%" height="100%">
                  <ComposedChart 
                    data={chartData}
                    margin={{ top: 40, right: 30, left: 20, bottom: 50 }}
                  >
                    <defs>
                      <filter id="candleShadow" x="-50%" y="-50%" width="200%" height="200%">
                        <feDropShadow dx="0" dy="1" stdDeviation="1" floodColor="rgba(0,0,0,0.1)" />
                      </filter>
                    </defs>
                    <CartesianGrid strokeDasharray="3 3" stroke="#f0f0f0" />
                    <XAxis 
                      dataKey="displayDate" 
                      stroke="#666"
                      fontSize={12}
                      tickLine={false}
                      interval="preserveStartEnd"
                    />
                    <YAxis 
                      stroke="#666"
                      fontSize={12}
                      tickLine={false}
                      domain={getYAxisDomain()}
                      tickFormatter={(value) => 
                        isKorean ? `${(value / 1000).toFixed(0)}K` : `${value.toFixed(0)}`
                      }
                    />
                    <Tooltip content={<CandlestickTooltip />} />
                    {/*    -   +  UI */}
                    <Bar 
                      dataKey="close" 
                      fill="transparent"
                      shape={(props) => {
                        const { payload, x, y, width, height, yAxisMap } = props;
                        
                        if (!payload || typeof payload.open !== 'number' || typeof payload.close !== 'number' || 
                            typeof payload.high !== 'number' || typeof payload.low !== 'number') {
                          return null;
                        }
                        
                        const { open, close, high, low } = payload;
                        
                        if (high < low || low <= 0 || high <= 0 || open <= 0 || close <= 0) {
                          return null;
                        }
                        
                        const isRising = close >= open;
                        const bodyColor = isRising ? '#e74c3c' : '#2980b9';
                        const fillColor = isRising ? '#e74c3c' : '#2980b9';
                        
                        const candleWidth = Math.min(width * 0.6, 12); //  
                        const wickWidth = 1.2;
                        const centerX = x + width / 2;
                        
                        //  Y     
                        const yDomain = getYAxisDomain();
                        const minPrice = yDomain[0];
                        const maxPrice = yDomain[1];
                        const domainRange = maxPrice - minPrice;
                        
                        if (domainRange === 0) {
                          return (
                            <g>
                              <line
                                x1={centerX - candleWidth / 2}
                                y1={y + height / 2}
                                x2={centerX + candleWidth / 2}
                                y2={y + height / 2}
                                stroke={bodyColor}
                                strokeWidth={2}
                              />
                            </g>
                          );
                        }
                        
                        //       Y  
                        const highY = y + ((maxPrice - high) / domainRange) * height;
                        const lowY = y + ((maxPrice - low) / domainRange) * height;
                        const openY = y + ((maxPrice - open) / domainRange) * height;
                        const closeY = y + ((maxPrice - close) / domainRange) * height;
                        
                        const bodyTop = Math.min(openY, closeY);
                        const bodyBottom = Math.max(openY, closeY);
                        const bodyHeight = Math.max(Math.abs(bodyBottom - bodyTop), 1);
                        
                        const isDoji = Math.abs(open - close) / Math.abs(high - low) < 0.02;
                        
                        return (
                          <g>
                            {/*   */}
                            <line
                              x1={centerX}
                              y1={highY}
                              x2={centerX}
                              y2={bodyTop}
                              stroke={bodyColor}
                              strokeWidth={wickWidth}
                              opacity={0.9}
                            />
                            
                            {/*   */}
                            <line
                              x1={centerX}
                              y1={bodyBottom}
                              x2={centerX}
                              y2={lowY}
                              stroke={bodyColor}
                              strokeWidth={wickWidth}
                              opacity={0.9}
                            />
                            
                            {/*     */}
                            {isDoji ? (
                              <line
                                x1={centerX - candleWidth / 2}
                                y1={closeY}
                                x2={centerX + candleWidth / 2}
                                y2={closeY}
                                stroke={bodyColor}
                                strokeWidth={2}
                              />
                            ) : (
                              <rect
                                x={centerX - candleWidth / 2}
                                y={bodyTop}
                                width={candleWidth}
                                height={bodyHeight}
                                fill={isRising ? fillColor : 'white'}
                                stroke={bodyColor}
                                strokeWidth={1}
                                rx={0.5}
                                ry={0.5}
                                opacity={isRising ? 0.8 : 1}
                              />
                            )}
                          </g>
                        );
                      }}
                      maxBarSize={18}
                    />
                    
                    {/*  */}
                    {showMovingAverages.ma5 && (
                      <Line
                        type="monotone"
                        dataKey="ma5"
                        stroke={movingAverageColors.ma5}
                        strokeWidth={1.2}
                        dot={false}
                        connectNulls={true}
                        activeDot={false}
                      />
                    )}
                    {showMovingAverages.ma20 && (
                      <Line
                        type="monotone"
                        dataKey="ma20"
                        stroke={movingAverageColors.ma20}
                        strokeWidth={1.2}
                        dot={false}
                        connectNulls={true}
                        activeDot={false}
                      />
                    )}
                    {showMovingAverages.ma60 && (
                      <Line
                        type="monotone"
                        dataKey="ma60"
                        stroke={movingAverageColors.ma60}
                        strokeWidth={1.2}
                        dot={false}
                        connectNulls={true}
                        activeDot={false}
                      />
                    )}
                    {showMovingAverages.ma120 && (
                      <Line
                        type="monotone"
                        dataKey="ma120"
                        stroke={movingAverageColors.ma120}
                        strokeWidth={1.2}
                        dot={false}
                        connectNulls={true}
                        activeDot={false}
                      />
                    )}
                  </ComposedChart>
                </ResponsiveContainer>
              ) : (
                <ResponsiveContainer width="100%" height="100%">
                  <LineChart 
                    data={chartData}
                    margin={{ top: 40, right: 30, left: 20, bottom: 50 }}
                  >
                    <CartesianGrid strokeDasharray="3 3" stroke="#f0f0f0" />
                    <XAxis 
                      dataKey="displayDate" 
                      stroke="#666"
                      fontSize={12}
                      tickLine={false}
                      interval="preserveStartEnd"
                    />
                    <YAxis 
                      stroke="#666"
                      fontSize={12}
                      tickLine={false}
                      domain={getYAxisDomain()}
                      tickFormatter={(value) => 
                        isKorean ? `${(value / 1000).toFixed(0)}K` : `${value.toFixed(0)}`
                      }
                    />
                    <Tooltip content={<LineTooltip />} />
                    <Line
                      type="monotone"
                      dataKey="close"
                      stroke="#667eea"
                      strokeWidth={2}
                      dot={false}
                      activeDot={{ r: 4 }}
                    />
                    
                    {showMovingAverages.ma5 && (
                      <Line
                        type="monotone"
                        dataKey="ma5"
                        stroke={movingAverageColors.ma5}
                        strokeWidth={1}
                        dot={false}
                        connectNulls={true}
                        activeDot={false}
                      />
                    )}
                    {showMovingAverages.ma20 && (
                      <Line
                        type="monotone"
                        dataKey="ma20"
                        stroke={movingAverageColors.ma20}
                        strokeWidth={1}
                        dot={false}
                        connectNulls={true}
                        activeDot={false}
                      />
                    )}
                    {showMovingAverages.ma60 && (
                      <Line
                        type="monotone"
                        dataKey="ma60"
                        stroke={movingAverageColors.ma60}
                        strokeWidth={1}
                        dot={false}
                        connectNulls={true}
                        activeDot={false}
                      />
                    )}
                    {showMovingAverages.ma120 && (
                      <Line
                        type="monotone"
                        dataKey="ma120"
                        stroke={movingAverageColors.ma120}
                        strokeWidth={1}
                        dot={false}
                        connectNulls={true}
                        activeDot={false}
                      />
                    )}
                    
                    <Line
                      dataKey={(data) => data.is_highest ? data.close : null}
                      stroke="#e74c3c"
                      strokeWidth={0}
                      dot={{ fill: '#e74c3c', r: 4 }}
                      activeDot={false}
                      connectNulls={false}
                    />
                    <Line
                      dataKey={(data) => data.is_lowest ? data.close : null}
                      stroke="#3498db"
                      strokeWidth={0}
                      dot={{ fill: '#3498db', r: 4 }}
                      activeDot={false}
                      connectNulls={false}
                    />
                  </LineChart>
                </ResponsiveContainer>
              )}
            </div>
            
            <div style={{ height: '25%' }}>
              <ResponsiveContainer width="100%" height="100%">
                <ComposedChart
                  data={chartData}
                  margin={{ top: 20, right: 30, left: 20, bottom: 40 }}
                >
                  <CartesianGrid strokeDasharray="3 3" stroke="#f0f0f0" />
                  <XAxis 
                    dataKey="displayDate" 
                    stroke="#666"
                    fontSize={10}
                    tickLine={false}
                    interval="preserveStartEnd"
                    height={30}
                  />
                  <YAxis 
                    stroke="#666"
                    fontSize={10}
                    tickLine={false}
                    domain={getVolumeDomain()}
                    tickFormatter={(value) => `${(value / 1000000).toFixed(1)}M`}
                    width={50}
                  />
                  <Tooltip 
                    formatter={(value) => [value?.toLocaleString(), '']}
                    labelStyle={{ color: '#333' }}
                  />
                  <Bar 
                    dataKey="volume" 
                    shape={(props) => <VolumeBar {...props} />}
                  />
                </ComposedChart>
              </ResponsiveContainer>
            </div>
          </div>
          </>
        ) : (
          <div style={{
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            height: '400px',
            color: '#666'
          }}>
              .
          </div>
        )}
      </ChartContent>
    </ChartContainer>
  );
};

export default StockChart;
