import api from './api';

export const portfolioService = {
  //  
  getPortfolio: async () => {
    try {
      const response = await api.get('/portfolio/');
      return response.data;
    } catch (error) {
      throw error.response?.data || { error: '  .' };
    }
  },

  //  
  buyStock: async (symbol, quantity) => {
    try {
      const response = await api.post('/portfolio/buy', {
        symbol,
        quantity
      });
      return response.data;
    } catch (error) {
      throw error.response?.data || { error: '  .' };
    }
  },

  //  
  sellStock: async (symbol, quantity) => {
    try {
      const response = await api.post('/portfolio/sell', {
        symbol,
        quantity
      });
      return response.data;
    } catch (error) {
      throw error.response?.data || { error: '  .' };
    }
  },

  //   
  getTransactions: async (limit = 50) => {
    try {
      const response = await api.get(`/portfolio/transactions?limit=${limit}`);
      return response.data;
    } catch (error) {
      throw error.response?.data || { error: '   .' };
    }
  },

  //    
  getMaxBuyQuantity: async (symbol) => {
    try {
      const response = await api.get(`/portfolio/max-buy/${symbol}`);
      return response.data;
    } catch (error) {
      throw error.response?.data || { error: '   .' };
    }
  },

  //   
  getPortfolioSummary: async () => {
    try {
      const response = await api.get('/portfolio/summary');
      return response.data;
    } catch (error) {
      throw error.response?.data || { error: '   .' };
    }
  }
};
